body {
  font-family: Montserrat, sans-serif;
  background-color: #FFFFFF;
  margin: 0;
  padding: 0; }

.dropbtn,
.navbtn {
  background-color: #137593;
  color: #FFFFFF;
  padding: 16px;
  font-size: 16px;
  border: none;
  font-weight: normal;
  font-size: 1.2em;
  text-decoration: none; }

.dropdown {
  position: relative;
  display: inline-block; }

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #0e576e;
  color: #FFFFFF;
  min-width: 160px;
  text-align: left;
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.6);
  z-index: 2000; }

.dropdown-content a {
  color: #FFFFFF;
  font-size: 1.2em;
  padding: 12px 16px;
  text-decoration: none;
  display: block; }

.dropdown-content a:hover {
  background-color: #0b4253;
  text-decoration: underline; }

.dropdown:hover .dropdown-content {
  display: block; }

.dropdown:hover .dropbtn,
.navbtn:hover {
  background-color: #0e576e; }

.header {
  padding: 10px;
  background-color: #137593;
  border-top: 2px solid #373738;
  border-bottom: 5px solid #004c64; }
  .header .logo {
    float: left;
    text-align: left;
    color: #FFFFFF;
    font-size: 2em;
    font-weight: 500;
    margin-top: 10px; }
    .header .logo a,
    .header .logo a:active,
    .header .logo a:visited,
    .header .logo a:hover {
      text-decoration: none;
      color: #FFFFFF; }
    .header .logo p {
      margin: 0; }
  .header .nav {
    float: right;
    text-align: right; }

@media only screen and (max-width: 768px) {
  .header {
    padding: 0;
    text-align: center;
    position: relative;
    z-index: 10000; }
    .header .col-2 {
      display: none; }
    .header .logo {
      float: none;
      display: block;
      width: 100%;
      text-align: center; }
    .header .nav {
      float: none;
      display: block;
      width: 100%;
      text-align: center;
      margin-bottom: 2px; } }

.hero {
  border-bottom: 5px solid #ccc;
  background-color: #FFFFFF;
  height: 300px;
  box-shadow: 0px 2px 0px 0px #575555; }
  .hero .hero-container {
    width: 100%;
    height: auto;
    position: relative;
    z-index: 1000; }
  .hero .hero-image {
    float: left;
    position: relative;
    z-index: -10000;
    top: 0px;
    left: 75%;
    max-width: 350px;
    max-height: 350px;
    overflow: hidden;
    padding: 0;
    margin: 0;
    border-radius: 0 0 50% 50%;
    border: 3px solid #FFFFFF;
    border-top: 0;
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.2); }
    .hero .hero-image img {
      border: 0;
      padding: 0;
      margin: 0;
      position: relative;
      left: -220px;
      top: -70px;
      height: auto;
      max-width: 800px; }
  .hero .hero-title {
    float: left;
    position: absolute;
    top: 80px;
    left: 10px;
    font-family: Passion One, sans-serif;
    font-size: 5.7em;
    color: #dc4205;
    -webkit-text-stroke: 1.5px #FFFFFF;
    text-shadow: 3px 2px 0px rgba(0, 0, 0, 0.4); }
  .hero .hero-subtitle {
    float: left;
    position: absolute;
    top: 180px;
    left: 10px;
    font-family: Open Sans, sans-serif;
    font-size: 3em;
    font-weight: normal;
    color: #137593; }
  .hero hr {
    border: 5px solid pink !important; }

@media only screen and (max-width: 768px) {
  .hero {
    float: none;
    display: block;
    width: 100%;
    text-align: center;
    height: auto; }
    .hero .col-2 {
      display: none; }
    .hero .hero-container {
      clear: both;
      height: auto; }
    .hero .hero-image {
      float: none;
      position: relative;
      left: auto;
      top: -100px;
      max-width: 100%; }
      .hero .hero-image img {
        max-width: 768px;
        margin: 0 auto;
        left: -180px;
        top: 50px; }
    .hero .hero-title {
      font-size: 4.5em;
      display: block;
      width: 100%;
      text-align: center;
      top: 25px;
      left: 0;
      line-height: 1em; }
    .hero .hero-subtitle {
      float: none;
      position: relative;
      font-size: 2.5em;
      display: block;
      width: 100%;
      text-align: center;
      clear: both;
      left: 0px;
      top: -50px;
      clear: both; } }

.cardset-container {
