.card {
  padding: 0px;
  margin: 10px;
  border-radius: 10px;
  border: 2px solid #FFFFFF;
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.6);
  background-color: #FFFFFF;
  max-width: 380px;
  height: 380px;
